.card {
  height: 135px;
  width: 95px;
  align-self: center;
  justify-self: center;
  border-radius: 12px;
  box-shadow: 0px 0px 4px rgb(136, 136, 136);
  background-color: rgba(233, 233, 233, 0.596);
  border: 1px solid rgb(184, 184, 184);
}

@media screen and (min-width: 768px){
	.card{
	height: 230px;
  width: 140px;
	}
}

@media screen and (min-width: 1024px){
	.card{
		height: 280px;
    width: 180px;
	}
}

@media screen and (min-width: 1440px){
  .card{
    height: 320px;
    width: 220px;
  }
}